import React from 'react'
import './Pokedex.css'
import PropTypes from 'prop-types'

class Pokemon extends React.Component {
  render() {
    return (
      <div className='pokemon-container'>
        <div className='pokemon-info'>
          <p>{this.props.poke.name}</p>
          <p>{this.props.poke.type}</p>
          <p>Average weight: {this.props.poke.averageWeight.value}{this.props.poke.averageWeight.measurementUnit}</p>
        </div>
        
        <img src={this.props.poke.image} alt='Pokemon' className='pokemon-img' />
      </div>
    )
  }
}

Pokemon.propTypes = {
  poke: PropTypes.shape({
    name: PropTypes.string,
    type: PropTypes.string,
    averageWeight: PropTypes.shape({
      value: PropTypes.number,
      measurementUnit: PropTypes.string
    }),
    image: PropTypes.string
  }).isRequired,
};

export default Pokemon